html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

.spacer {
  height: 24px;
}

.small-spacer {
  height: 12px;
}

.app {
  min-width: 1460px;
}